import {
	getJob,
	getArchives,
	getFocus,
	addFocus,
	delFocus,
	getCompanyDetail,
} from '@/api/job'
import {
	$wuxToast
} from '../../wxcomponents/wux/index'
import {
	debounce
} from '@/utils/index'
import QQMapWX from '../../common/qqmap-wx-jssdk.min.js';
var qqmapsdk;
export default {
	data() {
		return {
			det: true,
			focus: false,
			active: 0,
			detail: {
				nature: 1,
				size: 1,
				schooling: 1,
			},
			job: {
				list: null,
				count: null,
				search: {
					count: true,
					page: 1,
					limit: 10
				}
			},
			archives: {
				list: null,
				search: {
					arctype: 1,
					page: 1,
					limit: 5
				}
			},
			open: true,
		}
	},
	onShow() {
		if (this.$store.getters.register && this.detail.id) {
			this.getFocus()
			if (this.$store.getters.member.ac < 2) {
				this.$store.dispatch('MemberAC')
			}
		}
	},
	onPullDownRefresh() {
		this.refresher()
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		this.refresher()
	},
	onLoad(e) {
		if (e.det === 'stop') {
			this.det = false
		}
		this.getCompanyDetail(e.id)
		this.getArchives()
		qqmapsdk = new QQMapWX({
			key: 'I3ABZ-QPZW3-JIP3T-3AVMR-TRALT-77BFQ'
		});
	},
	methods: {
		getCompanyDetail(id) {
			getCompanyDetail({
				id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.detail = r.detail
					if (this.$store.getters.register && this.detail.id) {
						this.getFocus()
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		onClick(v) {
			const e = v.detail.name
			if (e === 1 && !this.job.list) {
				this.active = 1
				this.getJob()
			} else if (e === 1) {
				this.active = 1
			} else{
				this.active = 0
			}
		},
		getJob() {
			const s = this.job.search
			const t = this.job
			getJob({
				companyid: this.detail.id,
				count: s.count,
				page: s.page,
				limit: s.limit,
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.count = r.list.count
					if (t.list && t.list.length > 0) {
						t.list = t.list.concat(r.list.rows)
					} else {
						t.list = r.list.rows
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		refresher() {
			console.log(this.active)
			if (this.active === 1) {
				let t = this.job
				if (t.count > t.list.length) {
					t.search.page = t.search.page++
					this.getJob()
				} else {
					$wuxToast().hide()
					$wuxToast().show({
						type: 'forbidden',
						text: '已加载全部'
					})
				}
			}
		},
		detailTo(i, n) {
			if (n === 1) {
				uni.navigateTo({
					url: '/pages/company/detail?det=stop&id=' + i
				});
			} else if (n === 2) {
				uni.navigateTo({
					url: '/pages/job/detail?det=stop&id=' + i
				});
			} else if (n === 3) {
				uni.navigateTo({
					url: '/pages/article/detail?id=' + i
				});
			}
		},
		getFocus() {
			getFocus({
				id: this.detail.id
			}).then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.focus = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		addFocus(e) {
			if (this.$store.getters.register) {
				addFocus({
					id: this.detail.id
				}).then(response => {
					let r = response.data
					if (response.code === 20000 && r.add) {
						this.focus = true
						$wuxToast().show({
							type: 'success',
							text: '关注成功'
						})
					} else if (response.code === 20000 && r.detail) {
						this.focus = true
						$wuxToast().show({
							type: 'success',
							text: '已关注'
						})
					} else {
						$wuxToast().show({
							type: 'forbidden',
							text: '请求异常'
						})
					}
				})
			} else {
				// 跳转授权注册页面






				uni.navigateTo({
					url: '/pages/login/wx'
				});

			}
		},
		delFocus(e) {
			if (this.$store.getters.register) {
				delFocus({
					id: this.detail.id
				}).then(response => {
					let r = response.data
					if (response.code === 20000 && r.del) {
						this.focus = false
						$wuxToast().show({
							type: 'success',
							text: '已取消关注'
						})
					} else {
						$wuxToast().show({
							type: 'forbidden',
							text: '请求异常'
						})
					}
				})
			} else {
				// 跳转授权注册页面
				uni.navigateTo({
					url: '/pages/login/wx'
				});
			}
		},
		getArchives() {
			const t = this.archives
			getArchives(t.search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.list = r.list
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		openMap() {
			let o = this.open
			if (o) {
				o = false
				const d = this.detail
				qqmapsdk.geocoder({
					address: d.province + d.city + d.area + d.info.address,
					success: res => {
						const location = res.result.location
						uni.openLocation({
							latitude: location.lat,
							longitude: location.lng,
							success: function() {
								setTimeout(() => {
									o = true
								}, 2000)
							},
							fail: function() {
								o = true
							},
						});
					}
				});
			}
		}
	}
}
